.player {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.time-control {
  width: 50%;
  display: flex;
  color: white;
  text-shadow: 0px 0px 10px white;

  input {
    width: 100%;
    padding: 1rem 0rem;
    // -webkit-appearance: none;
    background-color: transparent;
    cursor: pointer;
  }

  p {
    padding: 1rem;
  }
}

.play-control {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;

  svg {
    cursor: pointer;
    color: white;
  }
}

@media screen and (max-width: 768px) {
  .time-control {
    width: 90%;
  }

  .play-control {
    width: 60%;
  }
}

